#root {
    position: absolute;
    width: 100%;
    height: 100%;
    font-family: Trebuchet, sans-serif;
    background: rgb(250, 230, 200);
    padding: 0;
    margin: 0;
}

body {
    padding: 0;
    margin: 0;
}

#wrapper {
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
}

#board {
    padding: 32px;
    border: 5px solid black;
    width: 50%;
    height: 80%;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}
table {
    padding: 5;
    border: none;
    flex-basis: auto;
}
tr {
    padding: none;
    margin: 0;
}
img {
    display: block;
    margin: none;
    padding: none;
    width: 100%;
    height: 100%;
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    -ms-user-drag: none;
    user-drag: none;
}

td {
    aspect-ratio: 1 / 1;
}
td.grid {
    aspect-ratio: 1 / 1;
    border: 1px dotted gray;
    border-collapse: separate;
    padding: 0;
    margin: 0;
    padding-bottom: 0px;
    cursor: pointer;
    position: relative;
}

td.out {
    aspect-ratio: 1 / 1;
    border-collapse: separate;
    padding: 0;
    padding-bottom: 0px;
}

img {
    position: relative;
    z: 0;
}

img.flow1 {
    position: absolute;
    top: 0;
    left: 0;
    z: -1;
}

img.flow2 {
    position: absolute;
    top: 0;
    left: 0;
    z: -2;
}

table, tbody {
    border-collapse: collapse;
    padding: 0;
    margin: 0;
    padding-bottom: 0px;
}

#bottom {
    width: 20%;
    height: 80%;
    display: flex;
    text-align: center;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 32px;
    border: 5px solid black;
    margin: 16px;
}


button {
    height: 64px;
    width: 100%;
    background: gray;
    border: 2px solid black;
    margin: 4px;
    border: none;
    font-size: 28px;
    cursor: pointer;
}

button.pbutton {
    animation: emphbutton 1s infinite;
}

td.gridout {
    border: 1px dotted gray;
    border-collapse: separate;
    padding: 0;
    margin: 0;
    animation: pulse 1s infinite;
    cursor: pointer;
}

#tiles {
    margin: 4px;
    margin-top: 10%;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    border: 2px solid black;
}

#tiles img {
    position: relative;
    aspect-ratio: 1 / 1;
    width: 48px;
    height: 48px;
}

.tile {
    position: relative;
    padding: 5%;
}

.tile p {
    color: black;
    font-size: 28px;
    aspect-ratio: 1 / 1;
    position: absolute;
    top: 0%;
    right: 0%;
    padding: 0;
    margin: 0;
    font-weight: 600;
}

#editor {
    width: 20%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 32px;
}

#editor textarea {
    resize: none;
    font-family: "Courier New", sans-serif;
    font-size: 16px;
    border: none;
    background: gray;
}

#editor textarea:focus {
    outline: none;
    background: white;
}

#editor button {
    background: green;
}
